import './App.css';
import { useState, useEffect } from 'react';

import Introduction from './components/introduction'
import AboutMe from './components/about_me'
import Experience from './components/experience'
import Projects from './components/projects'
import ContactMe from './components/contact_me'
import FloatingContactButton from './components/contact_button'
import LoadingPage from './components/loading'
import CustomCursor from "./components/cursor"

function App() {

  const [loading, setLoading] = useState(true);

  const [cursorVariant, setCursorVariant] = useState("default");

  useEffect(() => {
    setTimeout(() => {
      setLoading(false)
    }, 3000);
  }, []);

  return (
    <div className="bg-[#F0E8C6]">
      {loading ? (<LoadingPage />) :
          (
            <div>
              <CustomCursor cursorVariant={cursorVariant}/>

              <div className="relative z-10">

                <FloatingContactButton cursorStateChanger={setCursorVariant}/>

                <section id="intro">
                  <Introduction name = "BOY SUGIJAKKO"/>
                </section>

                <section>
                  <AboutMe cursorStateChanger={setCursorVariant}/>
                </section>
                
                <section>
                  <Experience/>
                </section>
                
                <section>
                  <Projects cursorStateChanger={setCursorVariant}/>
                </section>

                <section>
                  <ContactMe/>
                </section>

                <section>
                  <div className="bg-black py-8">
                    <div className="text-xs text-white m-auto text-center align-middle">
                      BOY SUGIJAKKO - 2022
                    </div>
                  </div>
                </section>

              </div>
            </div>
          )
      }
    </div>
  );
}

export default App;