import React, { useRef, useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMousePointer, faHandPointer } from '@fortawesome/free-solid-svg-icons'
import { motion } from "framer-motion";

function CustomCursor(props) {

    const cursorRef = useRef(null);
    const [cursorPos, setCursorPos] = useState({ x: 0, y: 0 });

    useEffect(() => {

        const handleMouseMove = (event) => {
            setCursorPos({ x: event.clientX, y: event.clientY });
        };

        window.addEventListener('mousemove', handleMouseMove);

        return () => {
            window.removeEventListener(
                'mousemove',
                handleMouseMove
            );
        };
    }, []);

    const cursorVariants = {
        default:{
            x: cursorPos.x,
            y: cursorPos.y
        }
    }

    return (
        <motion.div
            ref={cursorRef}
            className="fixed pointer-events-none z-50"
            variants={cursorVariants}
            transition={{ type:"spring", damping: 50, stiffness: 800}}
            animate={"default"}
        >
        <span className="text-white text-4xl">
            {props.cursorVariant === "default" ? 
            <FontAwesomeIcon icon={faMousePointer} className="cursor drop-shadow-md"  /> 
            : 
            <FontAwesomeIcon icon={faHandPointer} className="cursor drop-shadow-md" />
            }
        </span>
        </motion.div>
    );
}

export default CustomCursor;