import { motion } from "framer-motion";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAnglesDown } from '@fortawesome/free-solid-svg-icons'

function Introduction(props) {

  return (
    <div className="relative">
      <motion.div 
        className="flex h-screen" 
      >
        <div className="m-auto z-50">
          <motion.p 
            initial={{ opacity: 0, scale: 0.5, y: 1000 }}
            animate={{ opacity: 1, scale: 1, y: 0  }}
            transition={{ type:"spring", duration: 0.7, stiffness: 200, damping: 18 }}
            className='text-center text-2xl mb-3 text-stone-900 introduction'
          >Hi, my name is</motion.p>
          <motion.p 
            initial={{ opacity: 0, scale: 0.5, y: 1000 }}
            animate={{ opacity: 1, scale: 1, y: 0  }}
            transition={{ type:"spring", duration: 1, stiffness: 200, damping: 18 }}
            whileHover={{ scale: 1.3 }}
            className='text-center text-7xl text-[#C42D27] font-extrabold intro-name introduction'
          >{props.name}</motion.p>
        </div>

        <motion.div 
          animate={{ translateY: 15 }}
          transition={{ type: 'spring', ease: "linear", duration: 2, repeat: Infinity }}

          className="absolute bottom-10 w-screen text-center text-white text-3xl"
        >
          <FontAwesomeIcon icon={faAnglesDown} />
        </motion.div>
      </motion.div>
    </div>
  );
}

export default Introduction;
