
import { motion } from "framer-motion";
import { useEffect, useState } from "react";

function Backdrop(props){
  return (
      <motion.div
          onClick={props.onClick}
          className="fixed z-40 top-0 left-0 flex w-screen h-screen bg-opacity-90 bg-stone-900 justify-center items-center text-white"
          initial={{ opacity: 1 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
      >
          {props.children}
      </motion.div>
  );
};

const dropIn = {
  hidden: {
    y: "-100vh",
    opacity: 0,
  },
  visible: {
    y: "0",
    opacity: 1,
    transition: {
      duration: 0.1,
      type: "spring",
      damping: 25,
      stiffness: 500,
    },
  },
  exit: {
    y: "100vh",
    opacity: 0,
  },
};

function Modal (props){

  const [project, setProject] = useState(null);

  useEffect(() => {
    setProject(props.project);
  }, [props.project]);

  if (project === null) {
    return null;
  }

  return (
    <Backdrop onClick={props.handleClose}>
      <motion.div
          onClick={(e) => e.stopPropagation()}  
          className="modal orange-gradient"
          variants={dropIn}
          initial="hidden"
          animate="visible"
          exit="exit"
      >
          <div className="flex h-[90vh] w-[90vw] bg-white rounded-xl">
              <div className="basis-3/5 bg-stone-700">
                  <div className="flex flex-col items-center justify-center h-full pt-2">
                      <div className="mb-2">
                          <h1 className="text-2xl font-bold">
                              {project.title}
                          </h1>
                      </div>
                      <div className="grow bg-stone-800 w-11/12 m-auto py-2">
                          <p>
                              {project.shortDesc}
                          </p>
                      </div>
                      <div className="my-2">
                          {project.tags}
                      </div>
                  </div>
              </div>
              <div className="basis-2/5 bg-stone-400">
                  hello
              </div>
          </div>
          {/* <button onClick={handleClose}>Close</button> */}
      </motion.div>
    </Backdrop>
  );
};

export default Modal;