import { motion, useInView, AnimatePresence } from "framer-motion";
import { useRef, useState } from "react";
import useSound from 'use-sound';

import Modal from "./popup_modal"

import {projects} from '../data/projects';

import SwooshSFX from '../assets/swoosh.mp3';
import ButtonSFX from '../assets/button_click.mp3';

function NavigationBar(props){

    const ref = useRef(null)
    const isInView = useInView(ref, { once: true })

    const [playSound] = useSound(ButtonSFX, { volume: 0.75 });

    const handleClick = (button) => {
        // event.currentTarget.classList.toggle('filter-selection-active');
        props.filterStateChanger(button);
    };

    return(
        <motion.div
            ref={ref}
            style={{
                transform: isInView ? "none" : "translateX(-200px)",
                opacity: isInView ? 1 : 0,
                scale: isInView ? 1 : 0.5,
                transition: "all 0.8s cubic-bezier(.38,1.24,.95,1.12)"
            }} 
            className="text-white mb-3"
        >
            <span className="mr-2">Filter by:</span>
            <span 
                className={`mx-3 ${props.filterState === 'All' ? 'filter-selection-active' : ''}`} 
                onClick={() => handleClick('All')}
                onMouseDown={playSound}
                onMouseEnter={() => props.cursorStateChanger('hover')}
                onMouseLeave={() => props.cursorStateChanger('default')}
            >All</span>
            <span>/</span>
            <span 
                className={`mx-3 ${props.filterState === 'Web Development' ? 'filter-selection-active' : ''}`} 
                onClick={() => handleClick('Web Development')}
                onMouseDown={playSound}
                onMouseEnter={() => props.cursorStateChanger('hover')}
                onMouseLeave={() => props.cursorStateChanger('default')}
            >Web Development</span>
            <span>/</span>
            <span 
                className={`mx-3 ${props.filterState === 'Game Development' ? 'filter-selection-active' : ''}`} 
                onClick={() => handleClick('Game Development')}
                onMouseDown={playSound}
                onMouseEnter={() => props.cursorStateChanger('hover')}
                onMouseLeave={() => props.cursorStateChanger('default')}
            >Game Development</span>
            <span>/</span>
            <span 
                className={`mx-3 ${props.filterState === 'Others' ? 'filter-selection-active' : ''}`} 
                onClick={() => handleClick('Others')}
                onMouseDown={playSound}
                onMouseEnter={() => props.cursorStateChanger('hover')}
                onMouseLeave={() => props.cursorStateChanger('default')}
            >Others</span>
        </motion.div>
    );
}

function ProjectCard(props) {

    var tags = props.project.tags.map(tag => {
        return (
            <div className="p-1 bg-[#C42D27] w-fit rounded text-white text-xs">{tag}</div>
        );
    });

    const [playSoundSwoosh] = useSound(SwooshSFX, { volume: 0.25 });
    const [playSoundButton] = useSound(ButtonSFX, { volume: 0.75 });

    function onEnterAction(e) {
        // playSoundSwoosh();
        props.cursorStateChanger('hover');
    }

    function onClickAction(e) {
        playSoundButton();
    }

    if (props.activeFilter === 'All' || props.project.category === props.activeFilter) {
        return(
            <div>
                <motion.div 
                        // initial={{ opacity: 1, scale: 1, y: 0, zIndex: 0  }}
                        // whileHover={{ duration: 0.1, scale: 1.1, zIndex: 50 }}
                        // whileTap={{ scale: 1 }}
                        // onMouseEnter={onEnterAction}
                        // onMouseLeave={() => props.cursorStateChanger('default')}
                        // onMouseDown={onClickAction}

                    className={`flex flex-col rounded-xl m-auto h-full drop-shadow ${props.project.category}`}
                >
                    <div 
                        className="bg-[#F0E8C6] p-2 rounded-xl" 
                    >
                        <div className="bg-[#F0E8C6] p-1 rounded border-dashed border-2 border-stone-900 rounded-xl">
                            <div className="bg-stone-900 rounded-t-xl h-40 bg-center bg-cover bg-no-repeat" style={{backgroundImage: `linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0.5)),url(${props.project.image})`}}></div>
                            
                            <div className="flex flex-col grow rounded-b-xl py-2 px-2 bg-[#F0E8C6] w-full">
                                <p className="font-bold mb-3 text-center">{props.project.title}</p>
                                <p className="grow font-light text-sm mb-4">{props.project.shortDesc}</p>
                                <div className="flex flex-wrap gap-1">{tags}</div>
                            </div>
                        </div>
                    </div>
                </motion.div>
            </div>
        );
    }
}

function ProjectGallery(props) {
    
    const ref = useRef(null)
    const isInView = useInView(ref, { once: true })

    const [modalOpen, setModalOpen] = useState(false);

    console.log(modalOpen)
    
    return(
        <motion.div
            ref={ref}
            style={{
                transform: isInView ? "none" : "translateX(-200px)",
                opacity: isInView ? 1 : 0,
                scale: isInView ? 1 : 0.5,
                transition: "all 0.8s cubic-bezier(.38,1.24,.95,1.12)"
            }} 
            className="flex py-8 grid grid-cols-1 md:grid-cols-3 lg:grid-cols-5 gap-4 justify-items-stretch"
        >
            
            <AnimatePresence
                initial={false}
                exitBeforeEnter={true}
                onExitComplete={() => null}
            >
                {
                    modalOpen 
                    && 
                    <Modal 
                        modalOpen={modalOpen} 
                        handleClose={setModalOpen(false)}
                        project={projects[1]}
                    />
                }
            </AnimatePresence>

            {Object.keys(projects).map((key, index) =>
                <ProjectCard

                    key={projects[key].id}

                    project={projects[key]}
                    
                    cursorStateChanger={props.cursorStateChanger} 

                    activeFilter={props.filterState}

                    onMouseDown={() => alert('hi')}
                />
            )}
        </motion.div>
    );
}

function Projects(props) {

    const ref = useRef(null)
    const isInView = useInView(ref, { once: true })

    const [activeFilter, setActiveFilter] = useState('All');

    return (
        <div className="py-40 bg-[#1A3333]">
            <motion.h2 
                ref={ref}
                style={{
                    transform: isInView ? "none" : "translateX(-200px)",
                    opacity: isInView ? 1 : 0,
                    scale: isInView ? 1 : 0.5,
                    transition: "all 0.5s cubic-bezier(.38,1.24,.95,1.12)"
                }}

                className="text-center text-4xl text-white mb-24 font-extrabold"
            >
                PROJECTS
            </motion.h2>
                
            <div className="w-10/12 m-auto">
                <NavigationBar cursorStateChanger={props.cursorStateChanger} filterStateChanger={setActiveFilter} filterState={activeFilter}/>
                <ProjectGallery cursorStateChanger={props.cursorStateChanger} filterState={activeFilter}/>
            </div>

        </div>
    );
}

export default Projects;
