import { motion, useInView } from "framer-motion";
import { useRef } from "react";
import useSound from 'use-sound';

import ButtonSFX from '../assets/button_click.mp3';

import ProfilePic from "../assets/profile.png"
import CV from "../assets/CV.pdf"

import Lottie from 'react-lottie';
import Animation from "../assets/animation"

function AboutMe(props) {

    const [playSound] = useSound(ButtonSFX, { volume: 0.75 });
    
    const ref = useRef(null)
    const isInView = useInView(ref, { once: true })

    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: Animation,
        rendererSettings: { preserveAspectRatio: "xMidYMid slice" }
    };

    return (
        <div className="bg-[#1A3333] pt-24">
            <div className="p-3 w-10/12 m-auto bg-[#C42D27] rounded">
                <div className="flex items-center py-20 px-8 bg-[#C42D27] rounded border-dashed border-2 border-stone-900">
                    <div className="w-6/12 mr-16 grow">
                        <motion.div 
                            ref={ref}
                            style={{
                                transform: isInView ? "none" : "translateY(-200px)",
                                opacity: isInView ? 1 : 0,
                                scale: isInView ? 1 : 0.2,
                                transition: "all 0.8s cubic-bezier(.38,1.24,.95,1.12)"
                            }}
                        >
                            <h1 className="text-white text-4xl font-black mb-3">Let me introduce myself,</h1>
                            <p className="text-white text-justify font-light">
                                As a software engineer, I am skilled in designing, developing, and testing software applications to meet the needs of users. I have a strong background in computer science and a passion for problem-solving, which allows me to effectively troubleshoot and debug software to ensure its smooth and efficient operation. With my expertise in various programming languages, I am able to create robust and scalable software solutions that can be easily integrated into existing systems. Whether working independently or in a team environment, I am dedicated to delivering high-quality software that meets or exceeds customer expectations.
                            </p>
                        </motion.div>
                        <div className="mt-10">
                            <a href={CV} target="_blank" rel="noreferrer">
                                <motion.button 
                                    ref={ref}
                                    style={{
                                        transform: isInView ? "none" : "translateY(-200px)",
                                        opacity: isInView ? 1 : 0,
                                        scale: isInView ? 1 : 0.2,
                                        transition: "all 0.8s cubic-bezier(.38,1.24,.95,1.12)"
                                    }}
                                    className="py-2 px-3 bg-white rounded-lg"
                                    onMouseDown={playSound}
                                    onMouseEnter={() => props.cursorStateChanger('hover')}
                                    onMouseLeave={() => props.cursorStateChanger('default')}
                                >
                                    Download CV
                                </motion.button>
                            </a>
                        </div>
                    </div>

                    <div className="h-full items-center">
                        <motion.img 
                            ref={ref}
                            style={{
                                transform: isInView ? "none" : "translateY(-200px)",
                                rotate: isInView ? 0 : 180,
                                scale: isInView ? 1 : 0,
                                transition: "all 0.8s cubic-bezier(.38,1.24,.95,1.12)"
                            }}
                            
                            className="w-80 m-auto" 
                            src={ProfilePic} 
                            alt="profile picture"
                        />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default AboutMe;