import React, { useState } from 'react';
import { motion, Variants } from "framer-motion"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope, faXmark, faAddressBook, faUser } from '@fortawesome/free-solid-svg-icons'

import useSound from 'use-sound';

import ButtonSFX from '../assets/button_click.mp3';

const itemVariants: Variants = {
    open: {
      opacity: 1,
      x: 0,
      transition: { type: "spring", stiffness: 200, damping: 20 }
    },
    closed: { 
        opacity: 0, 
        x: -500, 
        transition: { duration: 0.4 } 
    }
  };

function FloatingContactButton(props) {

    const [isOpen, setIsOpen] = useState(false);
    const [playSound] = useSound(ButtonSFX, { volume: 0.75 });

    return (
        <motion.div
            initial={false}
            animate={isOpen ? "open" : "closed"}

            className="fixed z-20 pt-8 pl-8"
        >   
            <motion.button
                initial={{ opacity: 0, scale: 0.5, y: -100 }}
                animate={{ opacity: 1, scale: 1, y: 0  }}
                transition={{ type:"spring", duration: 0.4, stiffness: 100, damping: 10 }}
                whileHover={{ duration: 0.1, scale: 1.2 }}
                whileTap={{ scale: 0.8 }}
                onClick={() => {setIsOpen(!isOpen);}}
                onMouseDown={playSound}

                onMouseEnter={() => props.cursorStateChanger('hover')}
                onMouseLeave={() => props.cursorStateChanger('default')}

                className="bg-[#22736A] w-12 h-12 mb-5 z-50
                rounded-full drop-shadow-lg
                text-white text-2xl hover:bg-[#22736A] hover:drop-shadow-2xl"
            >   
                { isOpen ? <FontAwesomeIcon icon={faXmark} /> : <FontAwesomeIcon icon={faAddressBook} />}
            </motion.button>

            <motion.div 
                variants={itemVariants}
            >
                <a href="mailto:sugijakkoboy@gmail.com">
                    <div 
                        className="bg-white border-solid border-2 border-stone-900 mb-5 py-3 px-4 rounded-xl w-fit text-m"
                        onMouseDown={playSound}
                        onMouseEnter={() => props.cursorStateChanger('hover')}
                        onMouseLeave={() => props.cursorStateChanger('default')}
                    >
                        <FontAwesomeIcon icon={faEnvelope} />&ensp;&ensp;Email
                    </div>
                </a>
                <a href="https://www.linkedin.com/in/boy-sugijakko" target="_blank" rel="noreferrer">
                    <div 
                        className="bg-white border-solid border-2 border-stone-900 py-3 px-4 rounded-xl w-fit"
                        onMouseDown={playSound}
                        onMouseEnter={() => props.cursorStateChanger('hover')}
                        onMouseLeave={() => props.cursorStateChanger('default')}
                    >
                        <FontAwesomeIcon icon={faUser} />&ensp;&ensp;LinkedIn
                    </div>
                </a>
            </motion.div>
        </motion.div>
    );
  }
  
export default FloatingContactButton;