import { motion, useInView } from "framer-motion";
import { useRef } from "react";

function JobDetail(props){
    
    const ref = useRef(null)
    const isInView = useInView(ref, { once: true })

    return(
        <motion.div
            ref={ref}
            style={{
                transform: isInView ? "none" : "translateX(-200px)",
                opacity: isInView ? 1 : 0,
                scale: isInView ? 1 : 0.5,
                transition: "all 0.8s cubic-bezier(.38,1.24,.95,1.12)"
            }}
        >
            <p className="text-[#FED049] text-xl mb-1 font-bold">
                {props.title}
            </p>
            <p className="text-white text-l mb-2">
                ( {props.company} )
            </p>
            <div className="text-white text-sm font-light mb-12">
                {props.desc}
            </div>
        </motion.div>
    );
}

function Experience(props) {

    const ref = useRef(null)
    const isInView = useInView(ref, { once: true })

    return (
        <div className="pt-40 bg-[#1A3333]">

            <motion.h2 
                ref={ref}
                style={{
                    transform: isInView ? "none" : "translateX(-200px)",
                    opacity: isInView ? 1 : 0,
                    scale: isInView ? 1 : 0.5,
                    transition: "all 0.5s cubic-bezier(.38,1.24,.95,1.12)"
                }}

                className="text-center text-4xl text-white mb-24 font-extrabold"
            >
                EXPERIENCES
            </motion.h2>
            <motion.div 
                ref={ref}
                style={{
                    transform: isInView ? "none" : "translateX(-200px)",
                    opacity: isInView ? 1 : 0,
                    scale: isInView ? 1 : 0.5,
                    transition: "all 0.8s cubic-bezier(.38,1.24,.95,1.12)"
                }}
                className="w-8/12 m-auto p-3 bg-[#C42D27] rounded text-white"
            >
                <div className="h-full w-full py-12 bg-[#C42D27] rounded border-dashed border-2 border-stone-900">
                    <table className="m-auto align-top">
                        <tbody>
                        <tr>
                            <td className="bg-white"></td>
                            <td className="w-8">____&nbsp;&nbsp;&nbsp;</td>
                            <td>
                                <JobDetail 
                                    title="SENIOR SYSTEM ANALYST" 
                                    company="Bina Nusantara IT Division" 
                                    desc={
                                        <div className="pl-5">
                                            <ul className="list-disc"> 
                                                <li>Manage project timelines</li>
                                                <li>Design applications that are efficient to improve user experience and reduce system load</li>
                                                <li>Provide documentation (Data Flow Diagram, Entity Relationship Diagram, and Deployment Documents)</li>
                                            </ul>
                                        </div>
                                    }
                                />
                            </td>
                            <td className="align-top">
                                <span>OCT 2022 - PRESENT</span>
                            </td>
                        </tr>
                        <tr>
                            <td className="bg-white"></td>
                            <td className="w-8">____&nbsp;&nbsp;&nbsp;</td>
                            <td>
                                <JobDetail 
                                    title="SYSTEM ANALYST" 
                                    company="Bina Nusantara IT Division" 
                                    desc={
                                        <div className="pl-5">
                                            <ul className="list-disc"> 
                                                <li>Debug, fix, or enhance Stored Procedures used in application to fetch data from database</li>
                                                <li>Analyze requirements based on user needs</li>
                                                <li>Collaborate with programmers to develop new features</li>
                                                <li>Collaborate with programmers to enhance application to fit new user needs</li>
                                                <li>Trace errors caused by incorrect data on database</li>
                                            </ul>
                                        </div>
                                    }
                                />
                            </td>
                            <td className="align-top">
                                <span>OCT 2021 - OCT 2022</span>
                            </td>
                        </tr>
                        <tr>
                            <td className="bg-white"></td>
                            <td className="w-8">____&nbsp;&nbsp;&nbsp;</td>
                            <td>
                                <JobDetail 
                                    title="JUNIOR SYSTEM ANALYST" 
                                    company="Bina Nusantara IT Division" 
                                    desc={
                                        <div className="pl-5">
                                            <ul className="list-disc"> 
                                                <li>Collaborate with programmers to debug applications</li>
                                                <li>Assist seniors on solving application errors</li>
                                                <li>Assist users with problems on application usage</li>
                                            </ul>
                                        </div>
                                    }
                                />
                            </td>
                            <td className="align-top">
                                <span>MAR 2020 - OCT 2021</span>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </div>
            </motion.div>
        </div>
    );
}

export default Experience;
