import { motion, useInView } from "framer-motion";
import { useRef } from "react";
import useSound from 'use-sound';

import ButtonSFX from '../assets/button_click.mp3';

import EmailImage from "../assets/email.png"
import LineImage from "../assets/line.png"
import LinkedinImage from "../assets/linkedin.png"

function ContactMe(props) {

    const ref = useRef(null)
    const isInView = useInView(ref, { once: true })
    const [playSound] = useSound(ButtonSFX, { volume: 0.75 });
    
    return (
        <div className="py-20 bg-stone-900">
            <motion.h2 
                ref={ref}
                style={{
                    transform: isInView ? "none" : "translateX(-200px)",
                    opacity: isInView ? 1 : 0,
                    scale: isInView ? 1 : 0.5,
                    transition: "all 0.5s cubic-bezier(.38,1.24,.95,1.12)"
                }}
                className="text-center text-4xl text-white mb-32 font-extrabold"
            >
                CONTACT ME
            </motion.h2>
            <div class="grid grid-cols-3 text-center justify-items-center text-white">
                <div className="m-auto" onMouseDown={playSound}>
                    <img className="mb-3 m-auto" src={LineImage} width="80px" height="80px" alt=""/>
                    <span>ID: BOYSUGI</span> 
                </div>
                <div onMouseDown={playSound}>
                    <a href = "mailto: sugijakkoboy@gmail.com">
                        <img className="mb-3 m-auto" src={EmailImage} width="80px" height="80px" alt=""/>
                        <span>SUGIJAKKOBOY@GMAIL.COM</span> 
                    </a>
                </div>
                <div onMouseDown={playSound}>
                    <a href="https://www.linkedin.com/in/boy-sugijakko/">
                        <img className="mb-3 m-auto" src={LinkedinImage} width="80px" height="80px" alt=""/>
                        <span>LinkedIn - Boy Sugijakko</span> 
                    </a>
                </div>
            </div>
        </div>
    );
}

export default ContactMe;